<template>
  <div class="home">
    <van-row class="header-search">
      <van-col span="4">
        <div class="logo">
          <img src="../../assets/images/logo2.png" alt="">
          <h3>元気·咖啡</h3>
        </div>
      </van-col>
      <van-col span="18" offset="1">
         <van-search class="searchkuang"  disabled placeholder="搜索/元気能量/" @click="gesearch"/>
      </van-col>
    </van-row>

    <div class="header-bg">
      <img class="head-item-bg" src="https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2200903989,3832980799&fm=26&gp=0.jpg" alt="">
    </div>
  
    <div class="swiper-item">
      <van-swipe class="v-sper"  indicator-color="#ffffff" style="height:173.03px" :autoplay="5000">
        <van-swipe-item class="v-sper-item"  v-for="(item, index) in bannerData" :key="index">
          <img class="swiper-imgs auto-img" v-lazy="item.bannerImg" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    
    <div class="ad-text">
        <img src="../../assets/images/logo1.png" alt="">
        <div class="ad-text-word">
          <p class="word-a">" 元気·咖啡 "</p>
          <p class="word-b">—— 盛夏来袭，丝滑拿铁，元気满满 ——</p>
        </div>
    </div>

    <div class="menu-content">
      <div class="recom-title">
        <div class="re-item" v-for="(item, index) in typeData.slice(0,1)" :key="index" >{{item.typeDesc}}</div>
      </div>
      <div class="content clearfix">
        <div class="fl content-item" @click="goDetail(item.pid)" :class="[index % 2 == 0 ? 'left' : 'right']" v-for="(item, index) in productData" :key="index">
          <div>
            <img class="auto-img coffee-img" v-lazy="item.smallImg" alt/>
          </div>
          <div class="item-msg">
              <div class="pro-name one-text">{{item.name}}</div>
              <div class="pro-enname one-text">{{item.enname}}</div>
              <div class="pro-price">￥{{item.price}}</div>
          </div>
        </div>
        <div class="bottom-tips">
            <div class="tips">喜欢，你的元気满满~~~</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

//引入样式
import '../../assets/css/home.less'
import {createNamespacedHelpers} from 'vuex'
//mapState, mapMutations对应下边的两个解构。  menuModule是index.js的名称
const {mapState, mapMutations} = createNamespacedHelpers('menuModule')

export default {
  data() {
    return {
      //banner图
      bannerData: [],
      //商品类型
      typeData: [],
      //移动线条的top
      top: 0,
      //侧边栏菜单的标签高度
      // height: 0,
      //商品数据
      productData: []
    };
  },

  created() {
    //获取banner数据
    this.getBannerData();

    //获取商品类型数据
    this.getTypeData();

    //根据类型获取商品数据
    this.getProductByType(this.productType);

    this.moveLine();
  },
  //解构 //计算state.js里的数据
  computed: {
    ...mapState(['bannerDataCache', 'typeDataCache', 'products', 'height','productType'])
  },

  methods: {
    gesearch(){
      this.$router.push({name:'Search'})
    },
    //解构  mutations.js里的数据
    ...mapMutations(['changeData', 'changeProducts']),

    //初始化时移动线条
    moveLine() {
      // console.log('this.typeData ==> ', this.typeData);
      // console.log('this.height ==> ', this.height);

      for (let i = 0; i < this.typeData.length; i++) {
       //如果商品类型高亮，就当前下标*其高度就=top的距离
       if (this.typeData[i].isActive) {
          this.top = i * this.height;
          return;
        }
      }
      
    },

    //获取banner数据
    getBannerData() {
      //获取vuex的banner缓存数据，如果存在缓存数据，则不发起请求，否则发起请求
      if (this.bannerDataCache.length > 0) {
        // 把缓存的数据赋给到bannerData
        this.bannerData = this.bannerDataCache;
        // console.log('从缓存获取banner数据')
        return;
      }

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 通过 forbidClick 属性可以禁用背景点击
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

        //请求数据
      this.axios({
        method: 'GET',
        url: '/banner',
        //参数
        params: {
          appkey: this.appkey
        }
      }).then(result => {
        this.$toast.clear();
        // console.log('result', result);
        if (result.data.code == 300) {
          //把请求的数据result.data.result赋值给bannerData
          this.bannerData = result.data.result;
          // console.log('缓存banner数据');

          //缓存banner数据在vuex中
          // this.$store.commit('menuModule/changeData', {
          //   key: 'bannerDataCache',
          //   value: result.data.result
          // })

          // 缓存banner数据在vuex
          this.changeData({
            key: 'bannerDataCache', 
            value: result.data.result
          });

        }
      }).catch(err => {
        this.$toast.clear();
        // console.log('err', err);
      })
    },

    //获取商品类型数据
    getTypeData() {

      //获取vuex的侧边栏菜单缓存数据，如果存在缓存数据，则不发起请求，否则发起请求
      if (this.typeDataCache.length > 0) {
        // 把缓存的数据赋给到typeData
        this.typeData = this.typeDataCache;
        // console.log('从缓存获取侧边栏菜单数据')
        return;
      }

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        // 通过 forbidClick 属性可以禁用背景点击
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      //请求数据
      this.axios({
        method: 'GET',
        url: '/type',
        //参数
        params: {
          appkey: this.appkey
        }
      }).then(result => {
        this.$toast.clear();
        // console.log('result', result);
        if (result.data.code == 400) {
          //如果等于400，把获取的数据进行遍历高亮切换显示不高亮
          result.data.result.forEach(v => {
            v.isActive = false;
          })
          // unshift() 方法可向数组的开头添加一个或更多元素,并返回新的长度
          //把获取的数据的第一个高亮显示
          result.data.result.unshift({type: 'isHot', typeDesc: '最新推荐', isActive: true});
          // 把请求的数据赋值给商品类型typeData
          this.typeData = result.data.result;

          // this.$nextTick(() => {
          //   //获取侧边栏菜单标签高度
          //   let firstItemHeight = this.$refs.item0[0].clientHeight;
          //   // this.height = firstItemHeight;
          //   this.changeData({
          //     key: 'height',
          //     value: firstItemHeight
          //   })
          // })

          //缓存侧边栏菜单数据在vuex
          this.changeData({
            key: 'typeDataCache',
            value: result.data.result
          })
          // console.log('缓存侧边栏菜单数据');

        }
        
      }).catch(err => {
        this.$toast.clear();
        // console.log('err', err);
      })
    },

    //切换商品类型标签
    toggleAsideMenu(item, index) {
      if (item.isActive) {
        return;
      }

      for (let i = 0; i < this.typeData.length; i++) {
        if (this.typeData[i].isActive) {
          // 把其他的关闭高亮
          this.typeData[i].isActive = false;
          break;
        }
      }
      // 给当前的显示高亮
      item.isActive = true;

      //移动线条
      this.top = this.height * index;

      //记录当选选择的商品类型，主要纠正查看详情，再返回菜单，导致菜单数据显示不正确问题
      this.changeData({
        key: 'productType',
        value: {
          type: item.type
        }
      })
      
      //根据类型获取商品数据
      this.getProductByType(item);
    },

    //根据类型获取商品数据
    getProductByType(item) {
      // console.log('this.products', this.products);

      //在缓存获取商品数据
      if (this.products[item.type]) {
        this.productData = this.products[item.type];
        // console.log('从缓存获取商品数据');
        return;
      }

      this.productData = [];
      //声明参数
      let params = {
        appkey: this.appkey,
        key: 'type',
        value: item.type
      };

      //最近推荐
      if (item.type == 'isHot') {
        params.key = 'isHot';
        params.value = 1;
      }

      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });

      //请求数据
      this.axios({
        method: 'GET',
        url: '/typeProducts',
        // 把声明的参数进行请求
        params
      }).then(result => {
        this.$toast.clear();
        // console.log('result', result);
        if (result.data.code == 500) {
          //把请求的数据赋值给商品数据productData 
          this.productData = result.data.result;
          
          //缓存商品数据在vuex
          this.changeProducts({
            type: item.type,
            value: result.data.result
          })
          // console.log('缓存商品数据');
          
        }
      }).catch(err => {
        this.$toast.clear();
        // console.log('err', err);
      })
      
    },

    //点击商品进入详情页，获取对应数据(带id获取)
    goDetail(pid){
      //pid:商品id
      //query: {pid} 查询参数pid,用query,
      //跳转后会带参数进行跳转到页面
      this.$router.push({name:'Detail', query: {pid}})
    }

  }
};
</script>

<style lang="less" scoped>

</style>